<template>
    <v-container fluid>
        <v-timeline class="hidden-sm-and-down">
            <div class="py-5 display-2">
                Experience
            </div>

            <v-timeline-item
                v-for="entry in experience"
                :key="entry.sys.id"
                color="teal lighten-2"
                fill-dot
            >
                <entry-preview
                    :entry="entry"
                    v-on:launch-full-entry="launch_full_entry"
                ></entry-preview>
                <template v-slot:opposite>
                    <div class="title teal--text">
                        {{ entry.fields.startDate }} - {{ entry.fields.endDate }}
                    </div>
                </template>
            </v-timeline-item>
        </v-timeline>

        <v-timeline dense reverse class="hidden-md-and-up">
            <div class="py-5 display-2">
                Experience
            </div>

            <v-timeline-item
                v-for="entry in experience"
                :key="entry.sys.id"
                color="teal lighten-2"
                fill-dot
            >
                <entry-preview
                    :entry="entry"
                    v-on:launch-full-entry="launch_full_entry"
                ></entry-preview>
            </v-timeline-item>
        </v-timeline>

        <full-entry
            v-if="active_entry"
            :show="show_full_entry"
            :entry="active_entry"
            v-on:close-full-entry="show_full_entry = false"
        ></full-entry>
    </v-container>
</template>

<script>
import moment from 'moment'
import client from '@/plugins/api'

import entry_preview from './components/entry_preview'
import full_entry from './components/full_entry'


export default {
    components: {
        "entry-preview": entry_preview,
        "full-entry": full_entry
    },
    created: function() {
        // This API call will request an entry with the specified ID from the space defined at the top, using a space-specific access token.
        client.getEntries({
            content_type: "experience",
            order: "-fields.startDate"
        })
        .then(entries => {

            this.experience = entries.items.map((entry) => {
                entry.fields.startDate = this.format_date(entry.fields.startDate)
                entry.fields.endDate = this.format_date(entry.fields.endDate)
                return entry
            })
        })
    },
    data: function() {
        return({
            experience: [],
            active_entry: null,
            show_full_entry: false
        })
    },
    methods: {
        format_date: function(date) {
            if(date) {
                return moment(date).format("MMMM YYYY")
            }
            else {
                return 'Present';
            }
        },
        launch_full_entry: function(id) {

            // Update active entry
            this.active_entry = this.experience.filter((entry) => {
                return entry.sys.id === id
            })[0]
            // Activate visibility.
            this.show_full_entry = true
        }
    }
}
</script>
