<template>
    <v-container fluid>
        <div class="pa-5">
            <v-row>
                <v-col :cols="4" v-for="project in projects" :key="project.sys.id">
                    <v-card>
                        <v-img
                            :src="project.fields.images[0].fields.file.url"
                            :aspect-ratio="1"
                            position="bottom 50px center"
                        ></v-img>
                        <v-list-item>
                            <v-list-item-content>
                                <v-list-item-title class="headline">
                                    {{ project.fields.title }}
                                </v-list-item-title>
                                <v-list-item-subtitle>
                                    {{ project.fields.description }}
                                </v-list-item-subtitle>
                            </v-list-item-content>
                        </v-list-item>

                        <v-card-actions>
                            <v-btn @click="view_project(project.sys.id)" text class="teal--text">VIEW</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-col>
            </v-row>
        </div>

        <v-dialog v-model="show_dialog" fullscreen>
            <v-card v-if="active_entry">
                <v-toolbar class="black" dark>
                    <v-icon @click="close_dialog">clear</v-icon>
                </v-toolbar>

                <v-card-text class="white--text">
                    <v-list-item>
                        <v-list-item-content>
                            <v-list-item-title class="headline">
                                {{ active_entry.fields.title }}
                            </v-list-item-title>
                            <v-list-item-subtitle>
                                {{ active_entry.fields.description }}
                            </v-list-item-subtitle>
                        </v-list-item-content>
                    </v-list-item>
                </v-card-text>
                
                <v-carousel
                    :show-arrows="active_entry.fields.images.length > 1"
                    :hide-delimeters="active_entry.fields.images.length < 2"
                    :hide-delimiter-background="active_entry.fields.images.length < 2"
                >
                    <v-carousel-item>
                            <v-img v-for="image in active_entry.fields.images" :key="image.sys.id"
                            :src="image.fields.file.url"
                            contain
                            height="100%"
                        ></v-img>
                    </v-carousel-item>
                </v-carousel>
            </v-card>
        </v-dialog>
    </v-container>
</template>

<script>
import client from '@/plugins/api'

export default {
    created: function() {
        // This API call will request an entry with the specified ID from the space defined at the top, using a space-specific access token.
        client.getEntries({
            content_type: "Projects"
        })
        .then(entries => {

            this.projects = entries.items.map((entry) => {
                return entry
            })
        })
    },
    data: function() {
        return({
            active_entry: null,
            projects: [],
            show_dialog: false
        })
    },
    methods: {
        close_dialog: function() {
            this.show_dialog = false
            this.active_entry = null
        },
        view_project: function(id) {
            // Update active entry.
            this.active_entry = this.projects.filter((entry) => {
                return entry.sys.id === id
            })[0]
            // Activate visibility.
            this.show_dialog = true
        }
    }
}
</script>
