<template>
    <v-flex xs10 sm2 offset-xs1 offset-sm5>
        <v-dialog v-model="show_dialog" >
            <v-card>
                <v-toolbar class="black" dark>
                    <v-icon @click="$emit('close-full-entry')">clear</v-icon>
                </v-toolbar>
                <v-card-text class="black--text">
                    <v-row>
                        <v-col xs12 md6>
                            <!-- Position title. -->
                            <div class="display-2 pt-5 pb-3">
                                {{ entry.fields.title }}
                            </div>
                            
                            <!-- Relevant data. -->
                            <div>
                                <!-- Employer details. -->
                                <p>
                                    <v-icon>location_city</v-icon> {{ entry.fields.company }}
                                </p>
                                
                                <!-- Time employed. -->
                                <p>
                                    <v-icon>date_range</v-icon> {{ entry.fields.startDate }} - {{ entry.fields.endDate }}
                                </p>
                                
                                <!-- Description. -->
                                <p>
                                    {{ entry.fields.fullDescription }}
                                </p>
                            </div>
                        </v-col>

                        <v-col xs12 md6>
                            <!-- Location details. -->
                            <p>
                                <v-icon>location_on</v-icon> {{ entry.fields.location.lat }}, {{ entry.fields.location.lon }}
                            </p>

                            <GmapMap
                                :center="{ lat: entry.fields.location.lat, lng: entry.fields.location.lon }"
                                :zoom="14"
                                style="width: 75vw; height: 50vh"
                                >
                                <GmapMarker
                                    :position="{ lat: entry.fields.location.lat, lng: entry.fields.location.lon }"
                                    :clickable="true"
                                    :draggable="true"
                                    @click="center={ lat: entry.fields.location.lat, lng: entry.fields.location.lon }"
                                />
                            </GmapMap>
                        </v-col>
                    </v-row>
                </v-card-text>
            </v-card>
        </v-dialog>
    </v-flex>
</template>

<script>
export default {
    props: ["entry", "show"],
    computed: {
        show_dialog: {
            get: function() {
                return this.show
            },
            set: function() {
                this.$emit("close-full-entry")
            }
        }
    },
    data: function() {
        return({

        })
    }
}
</script>

